/**
 * uxunk
 *  redux 
 * /
 *  redux 
 * @param {Function} reducer state
 *
 * @param {Object} defaultState  state
 *
 * @param {Function} enhancer  redux 'applyMiddleware'
 *
 * @return {Object}  store  redux 
 */
export default function createStore(reducer, defaultState, enhancer) {
    //  defaultState  enhancer 
    if (typeof enhancer === 'undefined' && typeof defaultState === 'function') {
        enhancer = defaultState;
        defaultState = undefined;
    }
    //  createStore
    if (typeof enhancer === 'function') {
        return enhancer(createStore)(reducer, defaultState);
    }

    let currentState = defaultState;
    let currentReducer = reducer;
    const currentListeners = [];

    /**
     * dispatch  reducer  listener
     *
     * @param {Object} action /
     *
     * @return {Object}  action
     */
    function dispatch(action) {
        /**
         *  redux-thunk
         *  action  function 
         */
        if (typeof action === 'function') {
            action();
            return false;
        }
        currentState = currentReducer(currentState, action);
        currentListeners.forEach(item => item());
        return action;
    }

    /**
     * getState  state 
     */
    function getState() {
        return JSON.parse(JSON.stringify(currentState));
    }

    /**
     * subscribe   dispatch  state 
     *
     * @param {Function} fn  listeners 
     *
     * @return {Function} 
     */
    function subscribe(fn) {
        //  fn  function 
        if (!fn || typeof fn !== 'function') {
            throw Error('This function has been subscribed!');
        }
        // listeners 
        if (currentListeners.indexOf(fn) < 0) {
            currentListeners.push(fn);
        }
        //  listeners 
        return function unsubscribe() {
            const index = currentListeners.indexOf(fn);
            if (index > 0) {
                currentListeners.splice(index, 1);
            }
        };
    }

    /**
     * replaceReducer  reducer 
     *
     * @param {Function} newReducer  reducer
     */
    function replaceReducer(newReducer) {
        currentReducer = newReducer;
    }
    return {
        subscribe,
        dispatch,
        getState,
        replaceReducer
    };
}



