import compose from './compose';
/**
 * applyMiddleware 
 *  redux 
 *  dispatch 
 *  middlewares 
 * @param {...Function} middlewares 
 *
 * @return {Function}  createStore 
 *  store 
 */
export default function applyMiddleware(...middlewares) {
    /**
     *  createStore  enhancer  store 
     *  store  dispatch  reducer 
     * applyMiddleware 
     *  redux  createStore
     *  store  reducer  preloadedState
     *  store
     */
    return createStore => (...args) => {
        /**
         * 
         * ({ dispatch, getState }) => next => action => { ... }
         *  getState  dispatch 
         *  next  next  store  dispatch ()
         *  dispatch  action  redux 
         */
        const store = createStore(...args);
        /**
         *  dispatch  dispatch  dispatch 
         *  dispatch  store  dispatch dispatch
         *  middlewareAPI  getState  dispatch 
         *  dispatch 
         */
        let dispatch = () => {};

        const middlewareAPI = {
            getState: store.getState,
            dispatch: (...arg) => dispatch(...arg)
        };
        /**
         *  getState  dispatch 
         */
        const chain = middlewares.map(middleware => middleware(middlewareAPI));
        /**
         *  compose(...chain)
         *  (...arg) => 1(2(3(...arg))) 
         * compose(...chain)(store.dispatch)
         * arg = store.dispatch 3 next  store.dispatch 3 action => { ... }
         * 23 action => { ... } 2 next  action => { ... }
         * 1 action => { ... } 1 next 2 action => { ... } ,...
         * 1 action => { ... } 1 next 2 action => { ... } ,...
         * 3 next  store.dispatch 
         *  dispatch  store.dispatch 
         */
        dispatch = compose(...chain)(store.dispatch);

        /**
         *  store  dispatch  store
         */
        return Object.assign({}, store, { dispatch });
    };
}



