/**
 * bindActionCreators 
 *  action  dispatch 
 *  actionCreators  dispatch
 *
 * @param {Object} actionCreators  action  object
 *
 * @param {Function} dispatch  redux  createStore /
 *
 * @return {Object}  actionCreator  dispatch 
 */
export default function bindActionCreators(actionCreators, dispatch) {
    /**
     *  boundActionCreators 
     *  actionCreators  actionCreator
     *  actionCreator  action  arg
     * dispatch  actionCreator 
     *  (...arg) => dispatch(actionCreator(...arg)) 
     * actionCreator(...arg)  action
     * dispatch(action) /
     */
    const boundActionCreators = {};
    Object.keys(actionCreators).forEach((item) => {
        const actionCreator = actionCreators[item];
        boundActionCreators[item] = (...arg) => dispatch(actionCreator(...arg));
    });
    return boundActionCreators;
}



