/**
 * compose 
 * 
 * 
 *  func 
 * ...
 *
 * @param {...Function} funcs  function
 *
 * @return {Function}  reduce  a(b(c(d(...arg))))
 */
export default function compose(...funcs) {
    /**
     *  func  reduce 
     *  func = [a, b, c]
     *  reduce  (...arg) => a(b(...arg))
     *  reduce  (...arg) => ((...arg) => a(b(...arg)))(c(...arg))
     *  (...arg) => a(b(c(...arg)))
     *  c  c  b...
     */
    return funcs.reduce((a, b) => (...arg) => a(b(...arg)));
}



